<script>
  import { createEventDispatcher, onMount } from "svelte";
  import { globalplugin } from '../store/stores';
  import {
    ContentSwitcher, 
    Switch,
    Tile,
    } from "carbon-components-svelte";

  export let minutes;
  export let seconds;
  export let target;
  export let isPaused = false;
  let plugin;
  const unsubscribe = globalplugin.subscribe((value) => plugin = value)
  let pausebutton = `Pause`;
  let wotarget = "Full Body";

  const dispatch = createEventDispatcher();

  if(target == "full"){
    wotarget = "Full Body";
  }
  else if(target == "upper"){
    wotarget = "Upper Body";
  }
  else if(target == "lower"){
    wotarget = "Lower Body";
  }
  else { wotarget = "Core";}

  function setPause(){
    if (!isPaused) {
      isPaused = true;
      pausebutton = `Resume`;
    } 
    else {
      isPaused = false;
      pausebutton = `Pause`;
    }
}

  async function letEnd(){
  if (isPaused) {
    let res = await plugin.confirm('Really stop Session?', 'The current session will be ended');
      if(res.value) {
        dispatch("end");
      } else {
        console.log("They DID NOT CONFIRM it");
      }
  }
  }

  
</script>

<style>
  
</style>

{#if minutes >=0}
  {#if seconds > 9}
  <p>{wotarget} Workout {minutes}:{seconds}'</p>
  {:else}
  <p>{wotarget} Workout {minutes}:0{seconds}'</p>
  {/if}
  {/if}
  {#if minutes <0}
  <p>{wotarget} Workout 0:00'</p>
  {/if}

<Tile>
  
  <ContentSwitcher>

    <Switch text="Pause" style="font-size:75%" enabled={isPaused} on:click={setPause()}/>
    <Switch text="End" style="font-size:75%" enabled={!isPaused} on:click={letEnd()}/>

  </ContentSwitcher>

</Tile>
   